import React from 'react';
import { Input, Layout,Row, Col, DatePicker, Typography,Select, Table, Button, Tag } from 'antd';
import { StarOutlined, StarFilled } from '@ant-design/icons';
import 'antd/dist/antd.css';
import moment from 'moment'
import dayjs from 'dayjs'
import axios from 'axios';
import _ from 'lodash';
import numeral from 'numeral';
import {CopyToClipboard} from 'react-copy-to-clipboard';
// import numeral from 'numeral'

const { Title } = Typography;
const { RangePicker } = DatePicker;
const dateFormat = 'YYYY-MM-DD';
const { Option } = Select;
const cntr = [' United States',' United Kingdom',' Germany',' Israel',' Brazil','']

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      lastStart: moment().subtract(14, 'days').format('YYYY-MM-DD'),
      lastEnd: moment().subtract(7, 'days').format('YYYY-MM-DD'),
      num: 1,
      type: 1,
      new: 0,   
      data: []
    }
    this.reqHandler = this.reqHandler.bind(this);

  }



  componentDidMount() {

  }

  getRank = (itm) => {
    let top7 = ['United States','United Kingdom','Germany','Israel','Brasil','Mexico','Hong Kong','Singapore','Canada','','Brazil']
    let l2y = [moment().subtract(1,'year').format('YYYY'),moment().subtract(2,'year').format('YYYY'),'',moment().subtract(3,'year').format('YYYY')]
    let l4y = [moment().subtract(1,'year').format('YYYY'),moment().subtract(2,'year').format('YYYY'),moment().subtract(3,'year').format('YYYY'),moment().subtract(4,'year').format('YYYY'),'',moment().subtract(5,'year').format('YYYY')]
    let l5y = [moment().subtract(1,'year').format('YYYY'),moment().subtract(2,'year').format('YYYY'),moment().subtract(3,'year').format('YYYY'),moment().subtract(4,'year').format('YYYY'),moment().subtract(5,'year').format('YYYY'),'']
    let location = itm.location == '' ? '' : itm.location.split(',')[2]
    const ff = itm.founded ? moment(itm.founded).format('YYYY') : '';
    let aa = itm.rounds ? JSON.parse(itm.rounds).map(obj => {
      let bb = []
      if (obj.inv) {
        bb = obj.inv.map(oo => {
          return oo.rank
        })
      }
      return bb
    }) : []

    aa = _.flattenDeep(aa)
    aa = aa.length > 0 ? aa.filter(tt => {
      if (tt < 3000) {
        return true
      } else {
        return false
      }
    }) : 3
    let boo = aa == 3 ? false : aa.length > 0 ? true : false;
    // console.log(_.flattenDeep(aa))
    if (this.state.num == 1) {
      // console.log(boo)
      // if (top7.includes(itm.location) && itm.investors > 1 && itm.amount > 999999 && (l2y.includes(itm.founded) || itm.founded == '')   ) {
      if (_.indexOf(top7, location.trim()) >= 0 && itm.investors > 1 && itm.amount > 999999 && _.indexOf(l2y, ff) >= 0 ) {
        return 1
      } else if (_.indexOf(top7, location.trim()) >= 0 && itm.investors >= 1 && itm.amount > 499999 && _.indexOf(l2y, ff) >= 0  ) {
        return 2
      } else if (_.indexOf(top7, location.trim()) >= 0 && itm.investors == 0 && itm.amount > 499999 && _.indexOf(l2y, ff) >= 0  ) {
        return 3
      } else if (_.indexOf(top7, location.trim()) >= 0 && boo &&  _.indexOf(l2y, ff) >= 0) {
        return 4
      } else if (itm.crunch == 1) {
        return 5
      }
      // else if (_.indexOf(top7, location.trim()) >= 0 && boo &&  _.indexOf(l5y, ff) >= 0) {
      //   return 5
      // }
    } else if (this.state.num == 2) {
      // console.log(itm.name,itm.crunch)

      if (_.indexOf(top7, location.trim()) >= 0 && itm.investors > 1 && itm.amount > 999999 && _.indexOf(l4y, ff) >= 0 ) {
        return 1
      } else if (_.indexOf(top7, location.trim()) >= 0 && itm.investors >= 1 && itm.amount > 499999 && _.indexOf(l4y, ff) >= 0  ) {
        return 2
      } else if (_.indexOf(top7, location.trim()) >= 0 && itm.investors == 0 && itm.amount > 499999 && _.indexOf(l4y, ff) >= 0  ) {
        return 3
      } else if (_.indexOf(top7, location.trim()) >= 0 && boo &&  _.indexOf(l4y, ff) >= 0) {
        return 4
      } else if (itm.crunch == 1) {
        // console.log('tech')
        return 5
      }

    } else if (this.state.num == 3) {
      // console.log(itm.name,itm.crunch)

      if (_.indexOf(top7, location.trim()) >= 0 && itm.investors > 1 && itm.amount > 999999 && _.indexOf(l4y, ff) >= 0 ) {
        return 1
      } else if (_.indexOf(top7, location.trim()) >= 0 && itm.investors >= 1 && itm.amount > 499999 && _.indexOf(l4y, ff) >= 0  ) {
        return 2
      } else if (_.indexOf(top7, location.trim()) >= 0 && itm.investors == 0 && itm.amount > 499999 && _.indexOf(l4y, ff) >= 0  ) {
        return 3
      } else if (_.indexOf(top7, location.trim()) >= 0 && boo &&  _.indexOf(l4y, ff) >= 0) {
        return 4
      } else if (itm.crunch == 1) {
        // console.log('tech')
        return 5
      }

    } else if (this.state.num > 3) {
      // console.log(itm.name,itm.crunch)

      if (_.indexOf(top7, location.trim()) >= 0 && itm.investors > 1 && itm.amount > 999999 && _.indexOf(l4y, ff) >= 0 ) {
        return 1
      } else if (_.indexOf(top7, location.trim()) >= 0 && itm.investors >= 1 && itm.amount > 499999 && _.indexOf(l4y, ff) >= 0  ) {
        return 2
      } else if (_.indexOf(top7, location.trim()) >= 0 && itm.investors == 0 && itm.amount > 499999 && _.indexOf(l4y, ff) >= 0  ) {
        return 3
      } else if (_.indexOf(top7, location.trim()) >= 0 && boo &&  _.indexOf(l4y, ff) >= 0) {
        return 4
      } else if (itm.crunch == 1) {
        // console.log('tech')
        return 5
      }

    }
  }

  reqHandler = () => {
    var self = this;
  if (this.state.new == 0) {


    axios.get(`https://cb-viewer-back.useapp.in/data?lastStart=${this.state.lastStart}&lastEnd=${this.state.lastEnd}&num=${this.state.num}&type=${this.state.type}`) 
  .then(response => {
    // handle success
    const x = response.data.reduce((prev, item) => {

      var lbls = []
      var rnds = JSON.parse(item.rounds)
      if (rnds) {
      rnds.shift()
      if (rnds.length > 0) {
        var sum = 0
        rnds.map(it => {
          let dr = dayjs().diff(dayjs(it.date),'day')
          if (dr > 60) {
            sum = sum + it.vusd

          } else {
            lbls.push('CR')
          }

        })
        if (sum < 1000000 && item.prounds) {
          lbls.push('NO1')
        }
      }
    }
    if (!item.prounds) {
      if (item.founded != '') {
        if (parseInt(item.founded.split('-')[0]) > 2016) {
          lbls.push('New')
        }
      } else if (item.founded == '') {
        lbls.push('New')
      }

    } else {
      var ar1 = JSON.parse(item.prounds);
      var prsa = 0
      ar1.map(it => {
        prsa = prsa + it.vusd
      })
      if (prsa < 1000000) {
        lbls.push('NO2')
      }

    }

      console.log(lbls)


      prev.push(  {
        uuid: item.uuid,
        name: item.name,
        cbrank: item.cb_rank,
        newt: item.isnewtype ? item.isnewtype : '',
        cblink: item.cb_link,
        image: item.image,
        amount: item.amount,
        investors: item.investors,
        key: item.uuid,
        website: item.website,
        short_description: item.short_description,
        description: item.description,
        topinvestors: item.topinvestors,
        founded: item.founded == '' ? '' : item.founded.split('-')[0],
        location: item.location == '' ? '' : item.location.split(',')[2],
        lastdate: item.last_date.split('T')[0],
        round: item.number_round,
        key: item.id,
        s: item.stealth,
        c: item.crunch,
        rounds: JSON.parse(item.rounds),
        myrank: self.getRank(item),
        lbls: lbls,
        prounds: item.prounds ? JSON.parse(item.prounds) : ''
      });
      // console.log(_(cats).filter(['id', item.CategoryId]).value()[0].name);
      return prev;
    },[]);

    self.setState({data: x});
    // this.setState({data: _.values(response.data)}, () => console.log(this.state))
  })
  .catch(function (error) {
    // handle error
    console.log(error);
  });
} else if (this.state.new == 1) {


  axios.get(`https://cb-viewer-back.useapp.in/new?num=${this.state.num}`) 
.then(response => {
  // handle success
  const x = response.data.reduce((prev, item) => {

    var lbls = []
    var prsa = 0

    if (!item.prounds) {
      lbls.push('New')
    } else {
      var ar1 = JSON.parse(item.prounds);
      ar1.map(it => {
        prsa = prsa + it.vusd
      })
      console.log(prsa,item.amount,item.name)
      if (prsa != item.amount && Math.abs(item.amount-prsa) > 499999 && prsa < 999999) {
        console.log('here')
        lbls.push(numeral(item.amount-prsa).format('$0.0a'))
      }


        if (prsa < 1000000) {
          lbls.push('NO')
        }
  



    }
    if (item.rounds) {
      let aa = item.rounds ? JSON.parse(item.rounds).map(obj => {
        let bb = []
        if (obj.inv) {
          bb = obj.inv.map(oo => {
            return oo.rank
          })
        }
        return bb
      }) : []
  
      aa = _.flattenDeep(aa)
      aa = aa.length > 0 ? aa.filter(tt => {
        if (tt < 3000) {
          return true
        } else {
          return false
        }
      }) : 3
      let boo = aa == 3 ? false : aa.length > 0 ? true : false;



      if (item.prounds) {
        var ar1 = JSON.parse(item.prounds);
        ar1.map(it => {
          prsa = prsa + it.vusd
        })

        if (boo && item.amount < 1000000 && prsa < 100000) {
          lbls.push('TI')
        }
      }

    }




    prev.push(  {
      // name: item.name,
      // cbrank: item.cb_rank,
      // newt: item.isnewtype ? item.isnewtype : '',
      // cblink: item.cb_link,
      // image: item.image,
      // amount: item.amount,
      // investors: item.investors,
      // key: item.uuid,
      // website: item.website,
      // short_description: item.short_description,
      // description: item.description,
      // topinvestors: item.topinvestors,
      // founded: item.founded == '' ? 0 : item.founded.split('-')[0],
      // location: item.location == '' ? '' : item.location.split(',')[2],
      // lastdate: item.last_date.split('T')[0],
      // round: item.number_round,
      // key: item.id,
      uuid: item.uuid,
      name: item.name,
      cbrank: item.cb_rank,
      newt: item.prounds ? item.prounds : '',
      cblink: item.cb_link,
      image: item.image,
      amount: item.amount,
      investors: item.investors,
      key: item.uuid,
      website: item.website,
      short_description: item.short_description,
      description: item.description,
      topinvestors: item.topinvestors,
      founded: item.founded == '' ? '' : item.founded.split('-')[0],
      location: item.location == '' ? '' : item.location.split(',')[2],
      lastdate: item.last_date.split('T')[0],
      round: item.number_round,
      key: item.id,
      s: item.stealth,
      c: item.crunch,
      rounds: JSON.parse(item.rounds),
      myrank: self.getRank(item),
      lbls: lbls,
      prounds: item.prounds ? JSON.parse(item.prounds) : ''
    });
    // console.log(_(cats).filter(['id', item.CategoryId]).value()[0].name);
    return prev;
  },[]);

  self.setState({data: x});
  // this.setState({data: _.values(response.data)}, () => console.log(this.state))
})
.catch(function (error) {
  // handle error
  console.log(error);
});
}

  }

  releaseHandler = (date, dateString) => {
    this.setState({lastStart: dateString[0], lastEnd: dateString[1]});
   

  };
  whenHandler = (date, dateString) => {
    this.setState({whenStart: dateString[0], whenEnd: dateString[1]});

  };



  catHandler = val => {
    this.setState({catId: val});
  }
  ccHandler = val => {
    this.setState({num: val});
  }
  typeHandler = val => {
    this.setState({new: val});
  }
  typeWhenHandler = val => {
    this.setState({type: val});
  }
  rankHandler = (val, type) => {
    if (type == 1) {
      this.setState({rankStart: val.target.value});
    } else if (type == 2) {
      this.setState({rankEnd: val.target.value});
    } 
  }

  resetHandler = () => {
    axios.get(`https://cb-viewer-back.useapp.in/reset`) 
    .then(response => {
      // handle success
      console.log(response)
      // this.setState({data: _.values(response.data)}, () => console.log(this.state))
    })
    .catch(function (error) {
      // handle error
      console.log(error);
    });
    
  }

  starHandler = (uuid,s) => {
    axios.get(`https://cb-viewer-back.useapp.in/stealth?uuid=${uuid}&s=${s}`) 
    .then(response => {
      // handle success
      console.log(response)
      let nn = [...this.state.data];
      nn = nn.map(ii => {
        if (ii.uuid == uuid) {
          ii.s = s
        }
        return ii
      })
      console.log(nn)
      this.setState({data: nn})
    })
    .catch(function (error) {
      // handle error
      console.log(error);
    });

  }

  copyToClipboard = (e) => {
    // this.textArea.select();
    document.execCommand('copy');
    // This is just personal preference.
    // I prefer to not show the whole text area selected.
    // e.target.focus();
    this.setState({ copySuccess: 'Copied!' });
  };

  render() {

    const newr = this.state.new == 1 ? {
      title: 'NewType',
      dataIndex: 'newt',
    } : {};


    const columns = [
      {
        title: 'S',
        dataIndex: 'stealth',
        render: (text,record) => (record.s == 1 ? <StarFilled onClick={() => {this.starHandler(record.uuid,0)}}></StarFilled> : <StarOutlined onClick={() => {this.starHandler(record.uuid,1)}}></StarOutlined>),
      },
      {
        title: 'T',
        dataIndex: 'crunch',
        render: (text,record) => (record.c == 1 ? <div><img src={`https://cdn.iconscout.com/icon/free/png-64/techcrunch-458202.png`} style={{width: '32px', height: '32px'}}/></div> : <div></div>),
      },
      {
        title: 'TG',
        dataIndex: 'lbls',
        render: (text,record) => (record.lbls.length > 0 
          ? <>
              {record.lbls.map(tag => (
                <Tag color="blue" key={tag}>
                  {tag}
                </Tag>
              ))}
            </>
          : <div></div>),
      },
      {
        title: 'MR',
        dataIndex: 'myrank',
        defaultSortOrder: 'descend',
        sortDirections: ['descend'],
        sorter: (a, b) => {
          let va = parseInt(a.myrank) > 0 ? parseInt(a.myrank) : 10;
          let vb = parseInt(b.myrank) > 0 ? parseInt(b.myrank) : 10;
          return vb - va

        },
        // render: (text,record) => (record.c == 1 ? <div><img src={`https://cdn.iconscout.com/icon/free/png-64/techcrunch-458202.png`} style={{width: '32px', height: '32px'}}/></div> : <div></div>),
      },
      {
        title: 'Name',
        dataIndex: 'name',
        // render: (text,record) => (<div><img src={`https://res-3.cloudinary.com/crunchbase-production/image/upload/c_lpad,h_25,w_25,f_auto,b_white,q_auto:eco/${record.image}`} style={{marginRight: '10px'}} /><a href={`https://www.crunchbase.com/organization/${record.cblink}`} target="_blank">{text}</a><span><img src={`https://icons.iconarchive.com/icons/custom-icon-design/mono-general-2/24/copy-icon.png`} style={{marginLeft: '10px', width: '10px', height: '10px', opacity: '0.5'}} onClick={copy(`https://www.crunchbase.com/organization/${record.cblink}`)} /></span></div>),
        render: (text,record) => (<div><img src={`https://res-3.cloudinary.com/crunchbase-production/image/upload/c_lpad,h_25,w_25,f_auto,b_white,q_auto:eco/${record.image}`} style={{marginRight: '10px'}} /><a href={`https://www.crunchbase.com/organization/${record.cblink}`} target="_blank">{text}</a><span> <CopyToClipboard text={`https://www.crunchbase.com/organization/${record.cblink}`}>
        <img src={`https://icons.iconarchive.com/icons/custom-icon-design/mono-general-2/24/copy-icon.png`} style={{marginLeft: '10px', width: '10px', height: '10px', opacity: '0.5'}} />
      </CopyToClipboard></span></div>),
      },
      {
        title: 'Location',
        dataIndex: 'location',
        width: '200px',
        filters: [
          {
            text: 'Top 5',
            value: 0,
          },
          {
            text: 'Other',
            value: -1,
          },
        ],
        // specify the condition of filtering result
        // here is that finding the name started with `value`
        onFilter: (value, record) => {
          if (value == 0) {
           if ( cntr.includes(record.location) ) {
             return true
           } else {
             return false
           }
          } else if (value == -1) {
            if ( !cntr.includes(record.location) ) {
              return true
            } else {
              return false
            }
          }
        },
      },
      {
        title: 'Investors',
        dataIndex: 'investors',
        width: '20px',
        filters: [
          {
            text: '> 0',
            value: 0,
          },
          {
            text: '= 0',
            value: -1,
          },
        ],
        // specify the condition of filtering result
        // here is that finding the name started with `value`
        onFilter: (value, record) => {
          if (value == 0) {
           if ( record.investors > value ) {
             return true
           } else {
             return false
           }
          } else if (value == -1) {
            if ( record.investors == 0 ) {
              return true
            } else {
              return false
            }
          }
        },
      },
      {
        title: 'Amount',
        dataIndex: 'amount',
        width: '50px',
        render: (text,record) => (numeral(record.amount).format('$0.0a')),
        filters: [
          {
            text: '> $1M',
            value: 0,
          },
          {
            text: '< $1M',
            value: -1,
          },
        ],
        // specify the condition of filtering result
        // here is that finding the name started with `value`
        onFilter: (value, record) => {
          if (value == 0) {
           if ( record.amount > 999999 ) {
             return true
           } else {
             return false
           }
          } else if (value == -1) {
            if ( record.amount < 999999 ) {
              return true
            } else {
              return false
            }
          }
        },

      },
      {
        title: 'Founded',
        dataIndex: 'founded',
        width: '70px',
        filters: [
          {
            text: '> 2017',
            value: 0,
          },
          {
            text: '< 2017',
            value: -1,
          },
        ],
        // specify the condition of filtering result
        // here is that finding the name started with `value`
        onFilter: (value, record) => {
          if (value == 0) {
           if ( record.founded > 2017 || record.founded == 0 ) {
             return true
           } else {
             return false
           }
          } else if (value == -1) {
            if ( record.founded <= 2017 ) {
              return true
            } else {
              return false
            }
          }
        },
      },
      // {
      //   title: 'Last date',
      //   dataIndex: 'lastdate',
      // },
      {
        title: 'R. Number',
        dataIndex: 'round',
      },
      {
        title: 'Rank',
        dataIndex: 'cbrank',
      },
      newr

    ];

    return (
      <Layout style={{width: '100vw', height: '100vh'}}>
      <Row>
        <Col span={23}>
          <Row><Col>
        <Title level={4}>Last Fund Dates</Title>
        <RangePicker
      onChange={(date, dateString) => this.releaseHandler(date, dateString)}
        defaultValue={[moment(this.state.lastStart, dateFormat), moment(this.state.lastEnd, dateFormat)]}
        format={dateFormat}
      />
      </Col>
      <Col>
      <Title level={4}>R. number</Title>
      <Select defaultValue={this.state.num} style={{ width: 120 }}
            onChange={val => this.ccHandler(val)}
      >
      {countries.map(item => (<Option value={item}>{item == 4 ? '4+' : item}</Option>))}
      </Select>
      </Col>
      <Col>
      <Title level={4}>Investors</Title>
      <Select defaultValue={this.state.type} style={{ width: 120 }}
                  onChange={val => this.typeWhenHandler(val)}
      >
      {typeWhen.map(item => (<Option value={item}>{item}</Option>))}
      </Select>
      </Col>
      <Col>
      <Title level={4}>Is New?</Title>
      <Select defaultValue={this.state.new} style={{ width: 120 }}
                  onChange={val => this.typeHandler(val)}
      >
      {types.map(item => (<Option value={item}>{item}</Option>))}
      </Select>
      </Col>
      <Col>
      <Button type="primary" onClick={() => this.reqHandler()}> Search</Button>
      <Button onClick={() => this.resetHandler()}> Reset New</Button>
      </Col>
      </Row>
      <Row>
      <Table style={{marginTop: '20px', whiteSpace: 'pre'}}
          columns={columns}
          dataSource={this.state.data}
          pagination={{ position: ['topLeft', 'bottomLeft'], pageSize: 50 }}
          expandable={{
            expandedRowRender: record => 
            <div style={{width: '900px', color: '#000'}}>
              <p><strong>Website: </strong><a href={record.website} target="_blank">{record.website}</a></p>
              <p><strong>Title: </strong>{record.short_description}</p>
              {/* <p><strong>Top investors: </strong>{record.topinvestors}</p> */}
              <div>
              <table border='1'>
                <tr>
                  <th>Date</th>
                  <th>Round</th>
                  <th>Amount</th>
                  <th># Inv.</th>
                  <th>Investors</th>
                </tr>

                { record.rounds ?
                record.rounds.map(ob => {
                  const x = ob.inv.length > 0 ? ob.inv.map(it => <div><span style={{ color: `${it.rank < 3000 ? 'green' : '#000'}`, fontWeight: `${it.rank < 3000 ? 'bold' : 'normal'}`}}>{it.name} / R: {it.rank} / TI: {it.numr} / E: {it.exit} / L: {it.loc}</span><br /></div>) : null;
                  return (<tr><td>{ob.date}</td><td>{ob.name}</td><td>{numeral(ob.vusd).format('($0.0a)')}</td><td>{ob.ninv}</td><td>{x}</td></tr>)

                }) : <tr></tr>
                }
                </table>
              </div>
              <br />
              <p style={{wordWrap: 'break-word', width: 'inherit', whiteSpace: 'pre-wrap' }}><strong>Description: </strong>{record.description}</p>
            </div>,
          }}
        />
      </Row>
        </Col>
        <Col span={1}></Col>
      </Row>
      </Layout>
    );

  }
  
}
const types = [0,1];
const typeWhen = [0,1];
const countries = [1,2,3,4];
